

h1, h2, h3 {
  color: rgb(248, 37, 231);
  font-style: italic;
}

.body {
  text-align: center;
}
.message {
  color: grey;
  font-size: 20px;
  border-style: solid;
  border-radius: 2px;
  padding: 10px;
  margin-bottom: 10px;
}

ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
}

.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.notification {
  color: green;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px
}